<template>
  <div class="page">
    <Background />
    <AlertsContainer />
    <Header />
    <div class="about">
      <div class="description">
        <div class="text first-slide">
          {{ computedTimeline.current.desc }}
        </div>
        <div class="text second-slide">
          {{ computedTimeline.next.desc }}
        </div>
      </div>
      <div class="path">
        <div class="place first-slide"></div>
        <div class="place second-slide"></div>
      </div>
      <div class="description">
        <div class="year first-slide">{{ computedTimeline.current.year }}</div>
        <div class="year second-slide">{{ computedTimeline.next.year }}</div>
      </div>
      <div class="movement">
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
        <div class="arrow-down"></div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/header/Header.vue'
import Footer from '@/components/footer/Footer.vue'
import AlertsContainer from '@/components/partial/AlertsContainer'
import Background from '@/components/views/exchange/Background.vue'

export default {
  name: 'About',
  components: {
    AlertsContainer,
    Header,
    Footer,
    Background,
  },
  data: () => ({
    currentSlides: [0, 1],
    scrollPermit: true,
    changeSlide: true,
    current: 0,
    next: -1,
    prevSlide: '',
    years: [
      {
        year: 2014,
        desc: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Tenetur
          libero beatae consectetur reiciendis voluptatem quisquam saepe
          voluptatum error quae, autem dolorem maxime praesentium placeat sint,
          dolor voluptas nulla sed aperiam!`,
      },
      {
        year: 2015,
        desc: `Consectetur adipisicing elit. Officiis non
          qui earum maxime, quia hic perferendis voluptates placeat veniam
          recusandae repudiandae illum saepe numquam repellat. Necessitatibus
          sit voluptates architecto recusandae.`,
      },
      {
        year: 2018,
        desc: `Adipisicing elit. Voluptatem
          labore atque sunt? Odio minima, nulla doloribus nihil accusantium at
          doloremque earum pariatur nostrum corporis, accusamus neque.
          Temporibus praesentium quibusdam maiores.`,
      },
      {
        year: 2020,
        desc: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Tenetur
          libero beatae consectetur reiciendis voluptatem quisquam saepe
          voluptatum error quae, autem dolorem maxime praesentium placeat sint,
          dolor voluptas nulla sed aperiam!`,
      },
      {
        year: 2022,
        desc: `Consectetur adipisicing elit. Officiis non
          qui earum maxime, quia hic perferendis voluptates placeat veniam
          recusandae repudiandae illum saepe numquam repellat. Necessitatibus
          sit voluptates architecto recusandae.`,
      },
      {
        year: 2025,
        desc: `Adipisicing elit. Voluptatem
          labore atque sunt? Odio minima, nulla doloribus nihil accusantium at
          doloremque earum pariatur nostrum corporis, accusamus neque.
          Temporibus praesentium quibusdam maiores.`,
      },
    ],
  }),
  computed: {
    computedTimeline() {
      return {
        current: this.years[this.currentSlides[0]],
        next: this.years[this.currentSlides[1]],
      }
    },
  },
  methods: {
    go(direction) {
      let array = this.currentSlides
      let min = 0
      let max = this.years.length - 1

      if (direction > 0 && array[0] == max && array[1] == min) return [1, 2]
      else if (direction < 0 && array[0] == max && array[1] == min)
        return [max - 2, max - 1]
      else if (direction < 0 && array[0] == min && array[1] == min + 1)
        return [max - 1, max]

      this.currentSlides = array.map((s, i) => {
        let n = s + 2 * direction

        if (n < 0) {
          if (i == 0 && direction > 1) n = max - 1
          else n = max
        } else if (n > max) {
          if (i == 0) n = min
          else n = min + 1
        }

        return n
      })
    },
  },
  mounted() {
    document.body.style.overflowY = 'hidden'

    let self = this

    self.$anime({
      targets: '.arrow-down',
      delay: self.$anime.stagger(150),
      translateY: [0, 20, 0],
      rotate: ['45deg', '45deg'],
      opacity: [0.1, 1, 0.1],
      duration: 1000,
      loop: true,
      easing: 'easeInOutCubic',
    })

    this.$el.querySelector('.about').addEventListener('wheel', e => {
      if (self.scrollPermit) {
        var direction = e.wheelDelta < 0 ? 1 : -1
        self.scrollPermit = false

        // if (self.years.length - 1 === self.slide)
        // document.body.style.overflowY = 'auto'

        self.changeSlide = !self.changeSlide

        self.$anime({
          targets: ['.first-', '.second-'][+self.changeSlide] + 'slide',
          delay: self.$anime.stagger(150),
          translateY: [0, direction * 200],
          opacity: [1, 0],
          duration: 1000,
          easing: 'easeInOutCubic',
          complete() {
            console.log(
              ['First ', 'Second '][+self.changeSlide] + 'slide has gone!',
            )
          },
        })

        self.$anime({
          targets: ['.second-', '.first-'][+self.changeSlide] + 'slide',
          translateY: [direction * -200, 0],
          delay: self.$anime.stagger(150),
          opacity: [0, 1],
          duration: 1000,
          easing: 'easeInOutCubic',
          begin() {
            var firstSlide = +self.changeSlide

            if (firstSlide) {
              console.log('First slide is coming!')
            } else {
              console.log('Second slide is coming!')
            }
          },
          complete() {
            self.scrollPermit = true
          },
        })
      }
    })
  },
}
</script>

